								informacin y debido a eso, no tomar las decisiones indicadas para el mejoramiento de la
								misma. A raz de esa problemtica, ha ido surgiendo en la industria algo conocido como
								Business Intelligence (BI), que no es ms que la habilidad para transformar los datos,
								brindados por los estudios de indicadores, en informacin, y la informacin en conocimiento.
								</p>
