							<!-- End Portfolio Content -->

						</div>
					</div>
				</div>
			</div>
		</section>
		<!-- End Portfolio -->
</template>

<script>



export default {
  name: "OurPortafolio",
  components: {
  },
  data: () => ({
    categorias: [],
    servicios: []
  }),
  created() {
    this.listCategories();
    this.listServices();
  },
  methods: {
    listCategories() {
      axios
        .get("/api/categoria/list")
        .then((response) => {
          this.categorias = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
	},
	
    listServices() {
      axios
        .get("/api/articulo/list")
        .then((response) => {
          this.servicios = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
	},
  }
};
</script>

<style scoped>

@media screen and (min-width: 0px) and (max-width: 400px) {
  .mu-filter-item-content>p { display: none; }  /* show it on small screens */
}
</style>